import axios from 'axios'

if (typeof Promise.prototype.done !== 'function') {
    Promise.prototype.done = Promise.prototype.then
}

if (typeof Promise.prototype.fail !== 'function') {
    Promise.prototype.fail = Promise.prototype.catch
}

Promise.prototype.finally = function (callback) {
    let P = this.constructor
    return this.then(
        value => P.resolve(callback()).then(() => value),
        reason => P.resolve(callback()).then(() => {
            throw reason
        })
    )
}

export const config = {
    /** */
    // baseURL: process.env.VUE_APP_API_URL,
    timeout: 60000,
    responseType: 'json',
    method: 'post'
}

const handleError = (error) => {
    // 
    if (process.env.NODE_ENV === 'development') {
        console.log(error)
    }
}

const request = axios.create(config)

//
request.interceptors.request.use(
    (config) => {
        console.log(`:${config.url}`)
        return config
    },
    (error) => {
        // 
        return Promise.reject(error)
    }
)

request.interceptors.response.use(
    (response) => {
        if (!response) return Promise.reject()
        const res = response.data
        const data = res['1'] || res
        // const { resCode } = res
        // if (resCode && resCode!=200 || !resCode && data.errcode!="0"){
        //     handleError({ 'message': data.errdesc })
        //     return Promise.reject(data)
        // }
        return Promise.resolve(data == undefined ? {} : data)
    },

    (error) => {
        const status = error.response && error.response.status
        switch (status) {
            case 400: error.message = ''; break
            case 401: error.message = ''; break
            case 403: error.message = ''; break
            case 404: error.message = `: ${error.response.config.url}`; break
            case 408: error.message = ''; break
            case 500: error.message = ''; break
            case 501: error.message = ''; break
            case 502: error.message = ''; break
            case 503: error.message = ''; break
            case 504: error.message = ''; break
            case 505: error.message = 'HTTP'; break
            default: break
        }

        handleError(error)
        throw error
    }
)

export default request
