import $ from 'jquery';

/**
 *  when  context fn
 * periodic 
 * data 
 *
 */
const later = (fn, when, periodic, context, data) => {
    when = when || 0;
    let m = fn,
        d = $.makeArray(data),
        f,
        r;
    
    if (typeof fn == 'string') {
        m = context[fn];
    }
    
    if (!m) {
        //S.error('method undefined');
        // alert('method undefined');
        return null;
    }
    
    f = function () {
        m.apply(context, d);
    };
    
    r = (periodic) ? setInterval(f, when) : setTimeout(f, when);
    
    return {
        id: r,
        interval: periodic,
        cancel: function () {
            if (this.interval) {
                clearInterval(r);
            } else {
                clearTimeout(r);
            }
        }
    };
}

/**
 *  ms 
 *  ms  ms 
 */
const buffer = (fn, ms, context) => {
    ms = ms || 150;
    
    if (ms === -1) {
        return function () {
            fn.apply(context || this, arguments);
        };
    }
    let bufferTimer = null;
    
    function f() {
        f.stop();
        bufferTimer = later(fn, ms, 0, context || this, arguments);
    }
    
    f.stop = function () {
        if (bufferTimer) {
            bufferTimer.cancel();
            bufferTimer = 0;
        }
    };
    
    return f;
}

/**
 * ms
 * ms = 100, 1000ms 10000 10
 * 
 */
const fixInterval = (fn, ms, context) => {
    ms = ms || 150;
    
    if(ms === -1) {
        return function(){
            fn.apply(context || this, arguments);
        }
    }
    
    let going = false,
        bufferTimer = null;
    
    function f() {
        if(!going) {
            going = true;
            bufferTimer = later(function(){
                going = false;
                fn.apply(this, arguments);
            }, ms, 0, context || this, arguments);
        }
    }
    
    f.stop = function(){
        if(bufferTimer) {
            bufferTimer.cancel();
            going = false;
            bufferTimer = 0;
        }
    }
    
    return f;
}

export default {
    later,
    buffer,
    fixInterval
}
